.header__logo_text,
.text-slider {
  font-size: 13px;
  font-family: Helvetica;
  color: #fff;
  width: 485px;

}
.text-slider__content {
  list-style: none;
  position: relative;
  display: block;
  margin: 0 auto;
  padding: 0;
}
.text-slider__item {
  position: absolute;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
  left: 50%;
  top: 0;
  z-index: 0;
  opacity: 0;
  transition: opacity .5s ease;
  &.slider__item_show {
    opacity: .4;
  }
}
