.fast-buy__range {
  padding-top: 15px;
  .noUi-target {
    // width: 240px !important;
    margin-left: 15px;
    &:before {
      background-color: $colorDarkGreen;
      content: '';
      height: 2px;
      left: -15px;
      position: absolute;
      top: 0;
      width: 20px;
    }
  }
}
.fast-buy__range__minus,
.fast-buy__range__plus {
  cursor: pointer;
  font-size: 23px;
  fill: $colorGrey;
}
.fast-buy__range__val{
  width: 44px;
  font-size: 17px;
  font-family: sfL;

  -webkit-user-select: none;
  /* user-select -- это нестандартное свойство */
  -moz-user-select: none;
  /* поэтому нужны префиксы */
  -ms-user-select: none;
}

.fast-buy__range__amount {
  margin-bottom: 25px;
}
.fast-buy__range__step {
  margin-top: 5px;
  margin-left: -10px;
  width: 270px;
  font-size: 12px;
  color: $colorGrey;
  cursor: pointer;
}

.fast-buy__range__step li {
  display: block;
  float: left;
  width: 11.1%;
  text-align: center;

  -webkit-user-select: none;
  /* user-select -- это нестандартное свойство */
  -moz-user-select: none;
  /* поэтому нужны префиксы */
  -ms-user-select: none;
  &:nth-child(2) {
    width: 13.1%;
  }
  &:nth-child(3) {
    width: 15.2%;
  }
  &:nth-child(5) {
    width: 28%;
  }
  &:nth-child(6) {
    width: 19%;
    float: right;
    text-align: right;
  }
}