@import '_theme/dropdown__selected_theme_black.scss';
@import '_theme/dropdown__selected_theme_white.scss';
@import '_theme/dropdown__selected_theme_m.scss';
@import '_theme/dropdown__selected_theme_s.scss';
@import 'dropdown__selected__icon.scss';
.dropdown__selected {
  cursor: pointer;
  display: inline-block;
  position: relative;
  &:after {
    -o-transition: all .2s ease-in-out;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
  &_active {
    &.dropdown__selected__icon:after {
      -moz-transform: scale(1,-1);
      -webkit-transform: scale(1, -1);
      -o-transform: scale(1, -1);
      transform: scale(1, -1);
      filter: FlipV;
      -ms-filter: "FlipV";
    }
    &.dropdown__selected_bg {
      background-color: $colorDeepGreen;
      border-radius: 12px;
    }
  }
}
